import { Dispatcher } from "../redux/dispatcher/dispatcher";
import { DetailChord, DetailInit } from "../redux/store/scoreData";
import { Store } from "../redux/store/store";
import { playPiano } from "./melodyUtil";
import { getSymbolFromKey, IntervalRelationProps, KEY12_SHARP_LIST } from "./theoryUtils";

class HarmonizeUtil {

    static previewStart = (store: Store, dispatcher: Dispatcher) => {
        const state = store.scoreState;
        const sampler = store.instruments.sampler;
        const cacheList = state.elementCacheList;
        const chordList = store.scoreData.chordList;
        const reserveTasks = store.reserveTasks;
        let firstTime = -1;
        const update = () => {
            // if (store.invalidate.harmonize) {
            //     store.invalidate.harmonize();
            // }
            dispatcher.score.setScoreState(state);
        }
        // const reserveTasks = store.reserveTasks;
        const init = chordList[0].detail as DetailInit;
        const adjsut = 0;
        for (let i = state.focusIndex; i < chordList.length; i++) {
            const cache = cacheList[i];
            const element = chordList[i];
            if (element.type === 'chord') {
                const detail = element.detail as DetailChord;
                // 
                if (firstTime === -1) {
                    firstTime = cache.pastSecond;
                }


                if (detail.root == null) return;

                const rootIndex = (detail.root.index + init.keyIndex) % 12;
                const symbol = getSymbolFromKey(detail.symbolKey);
                const len = detail.beatLen + detail.minute.head + detail.minute.tail;

                const channelList = detail.backing.voicingList.map((item) => {
                    const interval = IntervalRelationProps[symbol.structs[item.struct]];
                    return 12 * item.octave + rootIndex + interval;
                });

                const chordTime = 60 / init.bpm * len;
                reserveTasks.push(
                    setTimeout(() => {
                        // sampler.releaseAll();
                        // stopPiano(store);
                        // store.melodyState.mouseState.curChord = element;
                        // dispatcher.tune.setState(store.melodyState);

                        channelList.forEach((index, j) => {
                            const soundName = KEY12_SHARP_LIST[index % 12];
                            const octave = Math.floor(index / 12);
                            const soundFullName = soundName + octave;
                            playPiano(soundFullName, store, chordTime);
                        });
                        state.focusIndex = i;
                        update();

                    }, cache.pastSecond - firstTime - adjsut)
                );
            }

            if (i === chordList.length - 1) {
                reserveTasks.push(
                    setTimeout(() => {
                        HarmonizeUtil.stopPreview(store, dispatcher, reserveTasks);
                    }, (cache.pastSecond - firstTime - adjsut))
                );
            }
        }
        state.isPreview = true;
        update();
    }

    static stopPreview = (store: Store, dispatcher: Dispatcher, reserveTasks: NodeJS.Timeout[]) => {
        const state = store.scoreState;
        state.isPreview = false;
        // store.instruments.sampler.releaseAll();
        // state.focusIndex = state.lastFocus;
        reserveTasks.forEach(id => clearTimeout(id));
        dispatcher.system.setReserveTasks([]);
        dispatcher.score.setScoreState(state);

        store.instruments.piano?.stop();
    }

    static previewCurHarmony = (store: Store) => {

        const state = store.melodyState;
        const chordList = store.scoreData.chordList;
        const init = chordList[0].detail as DetailInit;
        const curChord = state.mouseState.curChord;
        if (curChord != null) {

            const detail = curChord.detail as DetailChord;

            if (detail.root == null) return;

            const rootIndex = (detail.root.index + init.keyIndex) % 12;
            const symbol = getSymbolFromKey(detail.symbolKey);
            const len = detail.beatLen + detail.minute.head + detail.minute.tail;

            const channelList = detail.backing.voicingList.map((item) => {
                const interval = IntervalRelationProps[symbol.structs[item.struct]];
                return 12 * item.octave + rootIndex + interval;
            });

            const chordTime = 60 / init.bpm * 1;
            channelList.forEach((index) => {
                const soundName = KEY12_SHARP_LIST[index % 12];
                const octave = Math.floor(index / 12);
                const soundFullName = soundName + octave;
                playPiano(soundFullName, store, chordTime);
            });
        }
    }
}

export default HarmonizeUtil;
